Chengdu Pioneer Education")])])])])]),_c('div',{staticClass:"panel panel-default"},[_c('div',{staticClass:"panel-collapse collapse in"},[_c('div',{staticClass:"panel-body panel_text"},[_c('p',[_vm._v(" 先锋教育社区位于成都市武侯区天府四街仁美大源，风景宜人。在长期的教育实践中，先锋渐渐形成了自己的特点 ")]),_c('h4',[_vm._v("小规模、个性化教育")]),_c('p',[_vm._v(" 长期以来先锋的学生数量维持在20—30人内，我们认为最重要的是做符合先锋教育理念的教育，而非要做大做强。所以在相当长的一段时间内，先锋都维持这个规模不变。 小规模教学的好处在于师生比高，每个学生都可以和教师充分交流，获得教师充分的关注，可以根据每个学生的不同情况实施不同的教育，做到因材施教。另一个好处是彼此的情感连接很强，大家像一家人一样。 ")]),_c('h4',[_vm._v("师生\"同吃同住\"")]),_c('p',[_vm._v(" 先锋没有给教师单独的空间，没有所谓的独立出来的教师宿舍、食堂等设施。这样的安排给先锋带来了非常多的好处，如师生平等，学生安全的保证，更多的教育机会，亲密的学习共同体等等等等。 ")]),_c('h4',[_vm._v("提问式教育、项目式教学")]),_c('p',[_vm._v(" 体制内教育的核心之一是灌输式教育，视学生为接收、储存知识的容器。其隐藏的假设是学生一无所知，需要知识渊博的老师来帮助TA获取知识，TA是可以被控制的，学习的最终目的应该是适应社会，成为一个“精致的利己主义者”。 在先锋，最常见的教学方式是教师对学生的提问，像苏格拉底的精神助产术一样，帮助学生理清自己的想法，自己在做什么、到底要做什么。因为我们认为学生是充满求知欲的主动学习者，可以通过丰富的创造力和批判的精神去探究并积极面对现实生活。 先锋实施项目式教学已经有一段时间了，我们还鼓励学生在支教中自己当老师，亲自实施项目式教学。故差不多每个先锋的学生都渐渐熟悉、接受了项目式教学：以一个真实的问题为出发点，以项目的方式探究、解决这个问题，在整个项目的实施过程中完成对应的学习任务。 ")]),_c('h4',[_vm._v("丰富的活动")]),_c('p',[_vm._v(" 支教、音乐会、露营、骑行、节日庆祝、各种讲座……先锋有着丰富的“课外”活动，实质上先锋教育没有课内、课外之分。 ")]),_c('h4',[_vm._v("学生自治")]),_c('p',[_vm._v(" 先锋推崇学生自治，我们鼓励学生尽可能地参与到校园生活、管理的各个方面。2013年3月先锋教育刚刚开始实体办学时，就产生了第一届学生政府。学生政府做了很多有意思、有价值的事情：如制定各种规章制度、公约（教室管理、安全等等）、举行各类活动（节日庆祝、支教等）等。 ")])])])])],2),_c('el-collapse-item',{attrs:{"name":"3"}},[_c('template',{slot:"title"},[_c('div',{staticClass:"panel-heading",attrs:{"role":"tab"}},[_c('h4',{staticClass:"panel-title asd"},[_c('span',[_vm._v("→")]),_c('a',{staticClass:"pa_italic collapsed"},[_c('label',{staticClass:"horse2"},[_vm._v("教育理念 Educational Concept")])])])])]),_c('div',{staticClass:"panel panel-default"},[_c('div',{staticClass:"panel-collapse collapse in"},[_c('div',{staticClass:"panel-body panel_text"},[_c('div',{staticClass:"ins-details"},[_c('p',[_c('strong',[_vm._v("先锋的教育目标是“Good Character、Critical Thinking、Life-long Learning、Communication and Collaboration”（学会做人、学会思考、学会学习、学会交流、学会合作）。在先锋，一切教育实践都是围绕“五会”来开展的。")]),_c('br')]),_c('h4',[_vm._v("学会做人")]),_c('p',[_vm._v("主要指的是品格教育，即致力于让学生成为一个道德成熟、负责任、自律、有良好品格的人。")]),_c('h4',[_vm._v("学会思考")]),_c('p',[_vm._v("在思维能力中，先锋尤其强调批判性思维和创造性思维。 我们认为应试教育并没有提升学生的思维水平反而在全方面的压制、消除学生的思维。而许多问题的突破口就在于思维能力的提升。 除了提升具体的思维能力——分析能力；分辨假设、观点、论据等的能力；综合能力等——之外，我们认为批判性思维和创造性思维还包含着对应的精神气质，即不迷信权威，乐于独立思考，对外界保持开放的心态，尊重他人，积极自信等。 ")]),_c('h4',[_vm._v("学会学习")]),_c('p',[_vm._v(" 相对于体制内教育强调学习“知识”，先锋强调让学生掌握学习能力，不是“学会”，而是“会学”。因为在这个信息爆炸，知识快速更新的时代，仅仅掌握现有的知识是远远不够的，那会很快沦为新一代的“文盲”，这个时代要求学生成为一个终身学习者，自主学习者。 具体到学习过程。我们认为学习需要与自己的生命、生活相关。学生不是知识的容器，不是只能储存外在知识、客观知识的机器。相反，学生应该是有意义的主动建构者，是认知的主体。学生需要在生活中主动发现定义问题，确定学习目标，承担自己的责任并朝向目标努力，不断反思调控自身的学习，遇到困难和挑战也能坚持努力。 ")]),_c('h4',[_vm._v("学会交流")]),_c('p',[_vm._v(" 交流能力是一个人生存与发展的必备能力，也是决定一个人成功的必要条件。 有些人可能会认为，几乎所有的人张口就能说话，还需要学吗？我们认为说话不等于交流。与人交流是个复杂的过程。我们需要学习如何倾听他人，尊重并欣赏不同的观点，在情景中有礼貌的表达自己的情绪、想法，不讨好，不指责，最终达成有效的沟通。 ")]),_c('h4',[_vm._v("学会合作")]),_c('p',[_vm._v(" 现在的体制教育唯成绩论，非常强调竞争，事实上过度的竞争很容易造成孩子不敢面对失败，加剧他们的自我中心倾向。 不少家长甚至认为孩子只要成绩好就行，其他方面暂时不用管，他们恨不得孩子把自己的全部时间用在学习上，有些家长牺牲不少时间精力来帮孩子处理学习之外的事情，自己化身为一个完美的温室包围着孩子。最终结果是亲子关系紧张，孩子只会死学习，基本的生活自理能力都没有，更不用谈在当今时代至关重要的合作能力了。 ")])])])])])],2),_c('el-collapse-item',{attrs:{"name":"4"}},[_c('template',{slot:"title"},[_c('div',{staticClass:"panel-heading",attrs:{"role":"tab"}},[_c('h4',{staticClass:"panel-title asd"},[_c('span',[_vm._v("→")]),_c('a',{staticClass:"pa_italic collapsed"},[_c('label',{staticClass:"horse2"},[_vm._v("校园文化 Campus Culture")])])])])]),_c('div',{staticClass:"panel panel-default"},[_c('div',{staticClass:"panel-collapse collapse in"},[_c('div',{staticClass:"panel-body panel_text"},[_c('div',{staticClass:"ins-details"},[_c('p',[_c('strong',[_vm._v(" 安全、自由、责任、包容，这四点是先锋教育所认同的价值观。")])]),_c('h4',[_vm._v("安全")]),_c('p',[_vm._v(" 安全是先锋学校的底线。安全除了身体上的安全外，还包括心灵上的。身体上的安全主要指身体健康，充足的营养，良好的睡眠，减少意外事件的发生。心灵上的安全主要是让学生在先锋感受到足够的安全感，不用担心受到来自老师、家长、他人的无端指责。努力让学生可以在先锋放心、舒心，认为学校是可以信赖、值得依靠的。")]),_c('h4',[_vm._v("自由")]),_c('p',[_vm._v("自由并不是一件简单的事情。很多学生缺乏运用自由的能力，因为他们对自由怀有恐惧。习惯了过去被决定的生活，来到先锋自由的环境下会有种种不适，反而怀念过去一切被决定的安全感。 先锋首先做的是摆脱束缚，帮助学生尽力摆脱外界（学校、家庭）加给学生的不必要控制。这一过程需要家长的配合，也需要花费一定的时间，因为有的学生甚至已经将这些不必要的束缚内化了。 其次是打造一个支持环境，让学生可以在先锋这个环境下自由的探索自己的兴趣、志向所在。导师在这个过程中会给予必要的帮助和指导。 最后才是培养学生运用自由的能力，这包括了独立思考能力、权利意识、承担自己行动的后果等。")]),_c('h4',[_vm._v("责任 ")]),_c('p',[_vm._v("责任是与自由密切相关的，想享受自由，就需要有勇气做出选择，为自己的选择负责任。先锋的自由并不是肆无忌惮，没有规则的，而是建立在负责任的前提下的。")]),_c('p',[_vm._v(" 责任还与界限有关，学生需要去学习哪些是自己的责任，哪些是别人的。自己的责任必须由自己负责，别人不能越界。比如学习、生活是自己的事情，学生需要学会自己处理好，而不是由老师和家长操劳。")]),_c('p',[_vm._v("责任还和社会有关，学生需要学会看待社会现实，确定自己需要扮演的角色以及应该担负的责任。")]),_c('h4',[_vm._v("包容 ")]),_c('p',[_vm._v("包容首先指的是对多元价值的尊重。")]),_c('p',[_vm._v(" 先锋对学生的多元价值选择保持着包容的态度，我们尊重每一名学生的个人兴趣，给予学生最大的自由和宽容度，激励他们做自己想做的事。因为我们不认为学生成功的路径只有一条。我们相信教育是让每个学生自由的成长，找到属于他自己的价值。")]),_c('p',[_vm._v("包容还意味着开放。")]),_c('p',[_vm._v(" 先锋学校首先是向学生家长开放的，家长有权利了解学校的方方面面，也有足够的接口来参与学校的工作。其次，先锋向外界的同行是开放的。先锋乐意与各种NGO、国内外教育同行组织进行合作交流。最后，先锋是向未来开放的，我们从未觉得自己已经是最好，相反我们认为自己还有很多进步的空间，我们乐意根据环境、根据学生来调整自己，做更好的教育。")])])])])])],2)],1)],1)])])]),_c('div',{staticClass:"laboratory"},[_c('div',{staticClass:"container"},[_vm._m(3),_c('el-carousel',{attrs:{"interval":4000,"type":"card","height":"550px"}},_vm._l((_vm.items),function(item){return _c('el-carousel-item',{key:item.id,on:{"click":function($event){_vm.dialogVisible = true}}},[_c('el-card',{attrs:{"body-style":{ padding: '0px',  background: '#eee', cursor: 'pointer'},"shadow":"hover"}},[_c('div',{staticClass:"ser-img"},[_c('img',{staticStyle:{"height":"500px"},attrs:{"src":item.src}})]),_c('div',{staticStyle:{"padding":"14px"}},[_c('h3',[_vm._v(_vm._s(item.title))]),_c('p',[_vm._v(_vm._s(item.description))])])])],1)}),1)],1)])])}
